import React, {Component, Fragment} from 'react';
import {Card, DatePicker} from 'antd';
import {Chart, Axis, Geom, Tooltip} from 'bizcharts';
import {postAction} from "../../../../../axios";

import moment from 'moment';
const { MonthPicker } = DatePicker;

const curYear = new Date().getFullYear();
const curMonth = new Date().getMonth() + 1;
const monthFormat = 'YYYY-MM';


export default class JianKangZhuangTai extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [
                // { year: '第一周', '级别': 1 }
            ],
            dateString:curYear + "-" + curMonth,
            level:"",
            time: curYear + "-" + curMonth
        }
    }

    getData () {
        let dateTime = this.state.dateString.split("-")[1]
        if (dateTime ) {
            if (dateTime.length < 2) {
                dateTime = "0" + dateTime
            }
        }

        let date = this.state.dateString.split("-")[0] + "-" + dateTime;

        postAction(
            "/bigdata/mentality/findStudentMentalityInfo",
            {
                id: this.props.id,
                statisticMonth: date
            }
        ).then(
            (res) => {
                if (res.success) {
                    let newData = [];
                    res.obj.weekAlarmLevel.forEach(
                        (item) => {
                            newData.push({
                                year: "第" + item.statisticWeek + "周",
                                '级别': item.alarmLevel
                            })
                        }
                    )
                    this.setState({
                        data: newData,
                        level: res.obj.studentMentality.alarmLevelText
                    })
                } else {
                    this.setState ({
                        data:[],
                        level:""
                    })
                }
            }
        )
    }

    onChange = (date, dateString) => {

        // console.log(dateString,"date, dateString")

        this.setState({
            dateString: dateString,
            time:"请选择日期"
        },
            () => {
                this.getData()
            }
        )
    }


    render () {


        const label = {
            formatter: (text) => {
                if (Number(text) === 1) {
                    return ['红色预警']
                } if (Number(text) === 2) {
                    return ['橙色预警']
                } if (Number(text) === 3) {
                    return ['黄色预警']
                } if (Number(text) === 4) {
                    return ['蓝色预警']
                }else {
                    return ['未预警']
                }
            }
        }

        const scale = {
            '级别': {
                min: 0, // 定义数值范围的最小值
                max: 1, // 定义数值范围的最大值
                ticks: [0,1,2,3,4], // 用于指定坐标轴上刻度点的文本信息（label），当用户设置了 ticks 就会按照 ticks 的个数和文本来显示。
                // ticks: ['红色预警','橙色预警','黄色预警','蓝色预警','未预警']
            }
        };

        return (
            <Card
                title={<div className="common-title">心理健康状态</div>}
                headStyle={{ border: "none"}}
                bodyStyle={{paddingTop:0}}
                extra={
                    <div style={{float:"right"}}>
                        <MonthPicker onChange={this.onChange} defaultValue={moment(this.state.time)} format={monthFormat} />
                    </div>
                }
            >

                <Fragment>

                    <div style={{float:"left", lineHeight:"32px"}}>目前心理健康预警级别：
                        {
                            this.state.level === "红色预警"
                            ?
                                <span style={{color:"#E45A5A"}}>{this.state.level}</span>
                                :
                                this.state.level === "橙色预警"
                            ?
                                    <span style={{color:"#F5A623"}}>{this.state.level}</span>
                                    :
                                    this.state.level === "黄色预警"
                            ?
                                        <span style={{color:"#F8E71C"}}>{this.state.level}</span>
                                        :
                                        this.state.level === "蓝色预警"
                            ?
                                            <span style={{color:"#1890FF"}}>{this.state.level}</span>
                                            :
                                            <span style={{color:"#ccc"}}>{this.state.level}</span>
                        }

                    </div>

                </Fragment>

                <Chart
                    height={280}
                    data={this.state.data}
                    scale={scale}
                    padding={[10,40,40,80]}
                    forceFit
                >
                    <Axis name="year" />
                    <Axis name="级别" label={label} />
                    <Tooltip crosshairs={{type : "y"}}/>
                    <Geom
                        type="line"
                        position="year*级别"
                        size={2}
                        tooltip={['year*级别', (year, 级别)=>{
                            return {
                                name:"级别",
                                value:级别 === 1
                                ?
                                "红色预警"
                                :
                                    级别 === 2
                                    ?
                                    "橙色预警"
                                    :
                                        级别 === 3
                                        ?
                                        "黄色预警"
                                        :
                                            级别 === 4
                                            ?
                                            "蓝色预警"
                                            :
                                            "未预警"
                            }
                        }]}
                    />
                    <Geom
                        type='point'
                        position="year*级别"
                        size={4}
                        shape={'circle'}
                        style={{ stroke: '#fff', lineWidth: 1}}
                        tooltip={['year*级别', (year, 级别)=>{
                            return {
                                name:"级别",
                                value:级别 === 1
                                    ?
                                    "红色预警"
                                    :
                                    级别 === 2
                                        ?
                                        "橙色预警"
                                        :
                                        级别 === 3
                                            ?
                                            "黄色预警"
                                            :
                                            级别 === 4
                                                ?
                                                "蓝色预警"
                                                :
                                                "未预警"
                            }
                        }]}
                    />
                </Chart>
            </Card>
        )
    }

    componentDidMount () {
        this.getData()
    }
}


