import React, { Component } from 'react';
import { Card, DatePicker } from 'antd';
import { Chart, Axis, Geom, Tooltip } from 'bizcharts';
import NoData from '../../../../../common/components/NoData/NoDataComponent'

import moment from 'moment';

const { MonthPicker } = DatePicker;

export default class XiaJiangQuShi extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            dateString: "2019-01",
            level: ""
        }
    }

    onChange = (date, dateString) => {
        if (this.props.onChange){
            this.props.onChange(date, dateString)
        }
    }


    render() {

        const label = {
            formatter: (text) => {
                return `${text}%`
            }
        }

        const scale = {
            '趋势': {
                min: 0, // 定义数值范围的最小值
                max: 20, // 定义数值范围的最大值
                ticks: [0, 20, 40, 60, 80, 100], // 用于指定坐标轴上刻度点的文本信息（label），当用户设置了 ticks 就会按照 ticks 的个数和文本来显示。
            }
        };

        let title;
        if(this.props.type === 1){
            title = '生活'
        } else if (this.props.type === 2) {
            title = '学习'
        } else if (this.props.type === 3) {
            title = '社交'
        }

        const monthFormat = 'YYYY-MM';

        return (
            <Card
                className='card-noshadow'
                title={<div className="common-title">{title}功能下降趋势</div>}
                headStyle={{ border: "none" }}
                bodyStyle={{ paddingTop: 0, paddingBottom: 0 }}
                extra={<MonthPicker onChange={this.onChange} defaultValue={moment(this.state.time)} format={monthFormat}  />}
            >
                {
                    this.props.liveLine && this.props.liveLine.length > 0
                        ?
                        <Chart
                            height={280}
                            data={this.props.liveLine}
                            scale={scale}
                            padding={[10, 40, 40, 40]}
                            forceFit
                        >
                            <Axis name="year" />
                            <Axis name="趋势" label={label} />
                            <Tooltip crosshairs={{ type: "y" }} />
                            <Geom type="line" position="year*趋势" size={2} />
                            <Geom type='point' position="year*趋势" size={4} shape={'circle'} style={{ stroke: '#fff', lineWidth: 1 }} />
                        </Chart>
                        :
                        <NoData data='nodata' height={280} />
                }
            </Card>
        )
    }
}


