import React, {Component} from 'react';
import {Card, Table} from 'antd';


export default class GongNengXiangQing extends Component {


    render () {

        const columns = [
            {
                title: '项目',
                dataIndex: 'name',
                width: "30%"
            },
            {
                title: '状态',
                dataIndex: 'status',
                width: "20%",
                render: (text, record) => {
                    return (
                        text === 1
                        ?
                        <span title={text} style={{color:"#F04864"}}>异常</span>
                            :
                            <span title={text} style={{color:"#1890FF"}}>正常</span>
                    )
                }
            },
            {
                title: '详情',
                dataIndex: 'detail',
                render: (text, record) => {
                    return (
                        <span title={text}>{text}</span>
                    )
                }
            }
        ]

        let title;
        if(this.props.type === 1){
            title = '生活'
        } else if (this.props.type === 2) {
            title = '学习'
        } else if (this.props.type === 3) {
            title = '社交'
        }

        return (
            <Card
                title={<div className="common-title">{title}功能详情</div>}
                className='card-noshadow'
                headStyle={{ border: "none"}}
                bodyStyle={{paddingTop:0,paddingBottom:0}}
            >
                <Table
                    dataSource={this.props.liveTable}
                    pagination={false}
                    columns={columns}
                    bordered={true}
                    size="middle"
                />
            </Card>
        )
    }
}


