import React, {Component} from 'react';
import {Chart, Geom, Axis, Coord, Guide, Shape} from 'bizcharts';


export default class XueXi extends Component {

    //图形点击事件
    getType=(type)=>{
        this.props.getAllData(type)
    }

    render () {

        const {
            Html,
            Arc
        } = Guide;
        Shape.registerShape('point', 'pointer', {
            drawShape(cfg, group) {
                let point = cfg.points[0]; // 获取第一个标记点

                point = this.parsePoint(point);
                const center = this.parsePoint({
                    // 获取极坐标系下画布中心点
                    x: 0,
                    y: 0
                }); // 绘制指针

                group.addShape('line', {
                    attrs: {
                        x1: center.x,
                        y1: center.y,
                        x2: point.x,
                        y2: point.y - 20,
                        stroke: cfg.color,
                        lineWidth: 5,
                        lineCap: 'round'
                    }
                });
                return group.addShape('circle', {
                    attrs: {
                        x: center.x,
                        y: center.y,
                        r: 12,
                        stroke: cfg.color,
                        lineWidth: 4.5,
                        fill: '#fff'
                    }
                });
            }

        });
        const cols = {
            value: {
                min: 0,
                max: 9,
                tickInterval: 1,
                nice: false
            }
        };

        return (
            <div onClick={this.getType.bind(this,2)}>
            <Chart
                height={300}
                data={this.props.studyData}
                scale={cols}
                padding={[0, 0, 20, 0]}
                forceFit
            >
                <Coord type="polar" startAngle={-9 / 8 * Math.PI} endAngle={1 / 8 * Math.PI} radius={0.75} />
                <Axis name="value" zIndex={2} line={null} label={{
                    offset: -16,
                    textStyle: {
                        fontSize: 18,
                        textAlign: 'center',
                        textBaseline: 'middle'
                    }
                }} subTickCount={4} subTickLine={{
                    length: -8,
                    stroke: '#fff',
                    strokeOpacity: 1
                }} tickLine={{
                    length: -18,
                    stroke: '#fff',
                    strokeOpacity: 1
                }} />
                <Axis name="1" visible={false} />
                <Guide>
                    <Arc zIndex={0} start={[0, 0.965]} end={[9, 0.965]} style={{
                        // 底灰色
                        stroke: '#CBCBCB',
                        lineWidth: 18
                    }} />
                    <Arc zIndex={1} start={[0, 0.965]} end={[this.props.studyData[0].value, 0.965]} style={{
                        stroke: '#1890FF',
                        lineWidth: 18
                    }} />
                    <Html position={['50%', '95%']} html={() => `<div style="width: 300px;text-align: center;font-size: 12px!important;"><p style="font-size: 1.75em; color: rgba(0,0,0,0.43);margin: 0;">学习</p><p style="font-size: 3em;color: rgba(0,0,0,0.85);margin: 0;">${this.props.studyData[0].value * 10}%</p></div>`} />
                </Guide>
                <Geom type="point" position="value*1" shape="pointer" color="#1890FF" active={false} style={{
                    stroke: '#fff',
                    lineWidth: 1
                }} />
            </Chart>
            </div>
        )
    }
}


