import React, {Component} from 'react';
import {Row, Col} from 'antd';
import Info from './Info';
import {postAction} from "../../../../axios";
import {message} from "antd/lib/index";
import JianKangZhuangTai from "./JianKangZhuangTai";
import SheHuiGongNeng from "./SheHuiGongNeng";


export default class index extends Component {

    constructor(props){
        super(props);
        this.state = {
            studentInfo:{},
            studentNo:undefined,
            key:undefined
        }
    }

    getData(studentNo){
        let data = {studentNo}
        //学生信息
        postAction("/bigdata/student/getPageData", data).then(
            (res) => {
                if(res.success) {
                    this.setState({studentInfo: res.obj.list[0]})
                } else {
                    message.error(res.msg);
                }
            }
        )
    }

    componentWillMount() {
        if(this.props.match.params && this.props.match.params.code && this.props.match.params.id){
            let studentNo = this.props.match.params.code;
            let key = this.props.match.params.id;
            this.setState({studentNo,key},()=>{
                this.getData(studentNo,key);
            });
        }
    }

    render () {

        return (
            <Row>
                <Col span={5} style={{backgroundColor:"#fff", borderRadius:"4px", boxShadow:"0px 0px 10px 0px rgba(0,0,0,0.05",height:"1280px"}}>
                    <Info studentInfo={this.state.studentInfo} />
                </Col>

                <Col span={19} style={{paddingLeft:20}}>
                    <Row>
                        <Col span={24}>
                            <JianKangZhuangTai id={this.state.key} />
                        </Col>
                        <Col span={24} style={{marginTop:20}}>
                            <SheHuiGongNeng id={this.state.key} studentNo={this.state.studentNo} />
                        </Col>
                    </Row>
                </Col>
            </Row>
        )
    }
}


