import React, { Component } from 'react';


export default class WenTiXueSheng extends Component {

    render() {
        return (
            <div style={{ display: 'flex' }}>
                <span style={{
                    display: 'inline-block',
                    width: ' 160px',
                    whiteSpace: 'nowrap',
                    lineHeight: '25px',
                    marginRight: '40px'
                }}>当前状态在问题学生中的位置</span>
                <div style={{
                    height: '25px',
                    position: 'relative',
                    width: '100%'
                }}>
                    <div className="prossess">
                        <span style={{
                            float: 'left',
                            marginLeft: '5px'
                        }}>轻度</span>
                        <span style={{
                            float: 'right',
                            marginRight: '5px'
                        }}>重度</span>
                    </div>
                    <div
                        className={'prossessNow'}
                        style={
                            {
                                left: `${this.props.live * 100}%`
                            }
                        }
                    />
                </div>
            </div>
        )
    }
}


