import React, { Component } from 'react';
import { Row, Col, Card } from 'antd';
import ShengHuo from "./ShengHuo";
import XueXi from "./XueXi";
import SheJiao from "./SheJiao";

import './style.less';
import WenTiXueSheng from "./WenTiXueSheng";
import XiaJiangQuShi from "./XiaJiangQuShi";
import GongNengXiangQing from "./GongNengXiangQing";
import { postAction } from "../../../../../axios";


const curYear = new Date().getFullYear();
const curMonth = new Date().getMonth() + 1;

export default class SheHuiGongNeng extends Component {

    constructor(props) {
        super(props);
        this.state = {
            liveData: [{
                value: 0
            }],
            studyData: [{
                value: 0
            }],
            socialData: [{
                value: 0
            }],
            liveTable: [],
            liveLine: [],
            live:0,
            dateString:curYear + "-" + curMonth,
            time:curYear + "-" + curMonth,
            type:1
        }
    }

    getData() {
        let dateTime = this.state.dateString.split("-")[1]
        if (dateTime.length < 2) {
            dateTime = "0" + dateTime
        }

        let date = this.state.dateString.split("-")[0] + "-" + dateTime;

        postAction(
            "/bigdata/mentality/findStudentMentalityInfo",
            {
                id: this.props.id,
                statisticMonth: date
            }
        ).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        liveData: [
                            {
                                value: Math.round(res.obj.studentMentality.liveIndicators * 10)
                            }
                        ],

                        studyData: [
                            {
                                value: Math.round(res.obj.studentMentality.studyIndicators * 10)
                            }
                        ],

                        socialData: [
                            {
                                value: Math.round(res.obj.studentMentality.socialIndicators * 10)
                            }
                        ]
                    })
                } else {
                    this.setState({
                        liveData: [],
                        studyData: [],
                        socialData: []
                    })
                }
            }
        )
    }

    componentDidMount() {
        this.getData();
        this.getAllData(this.state.type);
    }

    //日期下拉选择
    onChange = (date, dateString) => {
        this.setState({
            dateString: dateString,
            time:"请选择日期"
        },
        () => {
            this.getAllData(this.state.type)
        })
        // console.log(date, dateString);
    }

    getAllData(type) {

        this.setState({
            type : type
        },() => {

            let dateTime = this.state.dateString.split("-")[1];
            if (dateTime ) {
                if (dateTime.length < 2) {
                    dateTime = "0" + dateTime
                }
            }

            let date = this.state.dateString.split("-")[0] + "-" + dateTime;

            //生活功能详情
            postAction(
                '/bigdata/mentality/findStudentMentalityInfo',
                {
                    "id": this.props.id,
                    "statisticMonth": date
                }
            ).then(res => {
                if (res.success) {
                    let newTable = []
                    if (type === 1) {
                        let liveData = JSON.parse(res.obj.studentMentality.liveSymptoms)
                        liveData.forEach((item, index) => {
                            newTable.push({
                                key: index + 1,
                                name: item.name,
                                detail: item.detail,
                                status: item.status
                            })
                        })
                    } else if (type === 2) {
                        let liveData = JSON.parse(res.obj.studentMentality.studySymptoms)
                        liveData.forEach((item, index) => {
                            newTable.push({
                                key: index + 1,
                                name: item.name,
                                detail: item.detail,
                                status: item.status
                            })
                        })
                    }else if (type === 3) {
                        let liveData = JSON.parse(res.obj.studentMentality.socialSymptoms)
                        liveData.forEach((item, index) => {
                            newTable.push({
                                key: index + 1,
                                name: item.name,
                                detail: item.detail,
                                status: item.status
                            })
                        })
                    }
                    this.setState({
                        liveTable: newTable,
                        type:type
                    })
                }
            })

            //生活功能下降趋势

            let data = {
                "id": this.props.id,
                "statisticMonth": date,
                "studentNo": this.props.studentNo
            }
            postAction(
                '/bigdata/mentality/findStudentMentalityTrend',data
            ).then(res => {
                if (res.success && res.obj.length > 0) {
                    let newLine = [];
                    if (type === 1) {
                        res.obj.forEach(item => {
                            newLine.push({
                                year: '第' + item.statisticWeek + '周',
                                '趋势': Math.round(Number(item.liveIndicators * 100)),
                            })
                        })
                    } else if (type === 2) {
                        res.obj.forEach(item => {
                            newLine.push({
                                year: '第' + item.statisticWeek + '周',
                                '趋势': Math.round(Number(item.studyIndicators * 100)),
                            })
                        })
                    }else if(type === 3){
                        res.obj.forEach(item => {
                            newLine.push({
                                year: '第' + item.statisticWeek + '周',
                                '趋势': Math.round(Number(item.socialIndicators * 100)),
                            })
                        })
                    }
                    this.setState({
                        liveLine: newLine,
                        type:type
                    })
                }
            })

            //当前状态在问题学生中的位置
            postAction(
                "/bigdata/mentality/findStudentMentalityProcess",
                {
                    "id":this.props.id,
                    "statisticMonth":date
                }
            ).then(
                (res) => {
                    if (res.success) {
                        if (type === 1){
                            this.setState({
                                live: res.obj.live
                            })
                        } else if (type === 2){
                            this.setState({
                                live: res.obj.study
                            })
                        } else if (type === 3){
                            this.setState({
                                live: res.obj.social
                            })
                        }
                    } else {
                        this.setState({
                            live:0
                        })
                    }
                }
            )
        })

    }

    render() {

        return (
            <Card
                title={<div className="common-title">社会功能情况</div>}
                headStyle={{ border: "none" }}
                bodyStyle={{ paddingTop: 0 }}
            >
                <Row>
                    <Col span={8}>
                        <ShengHuo getAllData={this.getAllData.bind(this)} liveData={this.state.liveData} />
                    </Col>
                    <Col span={8}>
                        <XueXi getAllData={this.getAllData.bind(this)} studyData={this.state.studyData} />
                    </Col>
                    <Col span={8}>
                        <SheJiao getAllData={this.getAllData.bind(this)} socialData={this.state.socialData} />
                    </Col>
                </Row>

                <Row>
                    <Col span={24} style={{ marginTop: 30, marginBottom: 30 }}>
                        <WenTiXueSheng live={this.state.live} />
                    </Col>

                    <Col span={12}>
                        <XiaJiangQuShi liveLine={this.state.liveLine} onChange={this.onChange} time={this.state.time} type={this.state.type}/>
                    </Col>

                    <Col span={12}>
                        <GongNengXiangQing liveTable={this.state.liveTable} type={this.state.type}/>
                    </Col>
                </Row>
            </Card>
        )
    }
}


