import React, {Component} from 'react';
import {postAction} from "../../../../axios";


export default class YuJingChuLi extends Component {

    constructor(props){
        super(props);
        this.state = {
            data:[
                // {time: "2018-11-15", results: "需要转院", note: "徐晓心理中心建议转介到协和医院心理科住院治疗" }
            ]
        }
    }

    getData () {
        postAction(
            "/bigdata/mentality/findStudentDeal",
            {
                studentNo: this.props.studentNo
            }
        ).then(
            (res) => {
                if (res.success) {
                    const obj = res.obj;
                    let newData = [];

                    obj.forEach(
                        item => {
                            newData.push({
                                time: item.updateDate,
                                // people: item,
                                results: item.mentalityDealResult ? item.mentalityDealResult : item.collegeDealResult ? item.collegeDealResult :"未处理",
                                note: item.mentalityDealRemark ? item.mentalityDealRemark : item.collegeDealRemark ? item.collegeDealRemark : "未处理",
                                people: item.mentalityDealUser
                                    ?
                                    item.mentalityDealUser
                                    :
                                    item.collegeDealUser
                                        ?
                                        item.collegeDealUser
                                        :
                                        "未处理",
                            })
                        }
                    )

                    this.setState({
                        data: newData
                    })
                } else {
                    this.setState({
                        data: []
                    })
                }
            }
        )
    }

    render () {
        return (
            <div style={{padding: "0px 20px"}}>
                <h4 style={{fontWeight:"600", fontSize: "16px"}}>警情处理</h4>
                <div style={{height: "580px", overflowY: "auto"}}>
                    {this.state.data.map((item, index) => {
                        return (
                            <div key={index} style={{marginBottom:"20px"}}>
                                <span style={{lineHeight:"35px"}}>第{index + 1}次处理</span> <br/>
                                <span style={{lineHeight:"35px"}}>处理时间：{item.time}</span> <br/>
                                <span style={{lineHeight:"35px"}}>处理人：{item.people}</span> <br/>
                                <span style={{lineHeight:"35px"}}>处理结果：{item.results}</span> <br/>
                                <span style={{lineHeight:"35px"}}>备注信息：{item.note}</span>
                            </div>
                        )
                    })}
                </div>

            </div>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


